import loadable from "@loadable/component";

import { Route, Routes, useLocation } from "react-router-dom";
import { QueryClient, QueryClientProvider } from "react-query";


const queryClient = new QueryClient();

const ResultsPage = loadable(() => import("pages/ResultsPage"));
const SelectionPage = loadable(() => import("pages/SelectionPage"));
const NoPage = loadable(() => import("pages/NoPage"));

function App() {
  
const location = useLocation();
    
  return (
    <Routes>
        <Route path="/" element ={ <SelectionPage />} />
        { location?.state?.results && <Route path={"/" + location.state.results} element = {<QueryClientProvider client={queryClient}>
            <ResultsPage />
          </QueryClientProvider>} />}
        <Route path="*" element={<NoPage />} />
        
    </Routes>
  );
}

export default App;
