import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { CurrencyCryptoArray } from "types";
import { createOptions } from "functions";

interface InitialState {
  allCryptos: null | CurrencyCryptoArray;
}
const initialState: InitialState = {
  allCryptos: null,
};

const list = createSlice({
  name: "list",
  initialState,
  reducers: {
    setListOfAllCryptos: {
      reducer: (state, action: PayloadAction<CurrencyCryptoArray>) => {
        state.allCryptos = action.payload;
      },
      prepare: (resp: any) => {
        return { payload: createOptions(resp) };
      },
    },
  },
});

export default list.reducer;
export const { setListOfAllCryptos } = list.actions;
export const getListOfCryptos = (state: { list: { allCryptos: CurrencyCryptoArray } }) => state.list.allCryptos;
